.button {
  position: fixed;
  bottom: 30px;
  right: 30px;
  border: none;
  outline: none;
  background-color: var(--danger-color);
  color: #000;
  cursor: pointer;
  padding: 15px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  opacity: 0;
  z-index: 99;
}

.button:hover {
  background-color: #a70000;
  transition: all 0.3s;
}

.show {
  opacity: 1;
  transition: opacity 0.3a ease-in;
}

.icon {
  font-weight: bold;
  color: #fff;
}
