.content {
  overflow: hidden;
}

.testimonial {
  max-width: 700px;
  height: 170px;
  margin-top: 3.5rem;
  margin-bottom: 40px;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.fade-in {
  opacity: 1;
}

.message {
  font-size: 1.2rem;
  line-height: 1.7;
  margin-bottom: 10px;
  font-style: italic;
  transition: all 0.6s ease;
}

.name {
  font-size: 1.3rem;
  font-weight: bold;
  position: relative;
  padding-top: 30px;
  padding-left: 60px;
  transition: all 0.6s ease;
}
.name::before {
  content: "";
  position: absolute;
  left: 0px;
  bottom: 10px;
  width: 36px;
  height: 1px;
  background-color: black;
  transform: translateY(-50%);
  transition: all 0.6s ease;
}

.buttons {
  display: flex;
  margin-top: 10px;
  gap: 20px;
}
.slider-button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  margin-right: 10px;
  cursor: pointer;
  color: #777;
  transition: color 0.3s;
}

.slider-button:hover {
  color: var(--danger);
  transform: scale(1.1);
  transition: all 0.5s ease;
}

@media screen and (max-width: 768px) {
  .buttons {
    margin-top: 5rem;
  }
}
