.home {
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  line-height: 1;
  height: calc(100vh - 5.5rem);
}

.home-content {
  margin-bottom: 100px;
}
.home-title {
  align-items: start;
}
.home-title h2 {
  margin-bottom: 1rem;
  transform: translateY(500px);
  animation: SlideUp 0.8s ease forwards 0.5s;
}

.home-title h1 {
  font-size: 5rem;
  font-weight: 700px;
  margin-left: -5px;
  transform: translateY(500px);
  animation: SlideUp 0.8s ease forwards 0.7s;
}
.text-wrapper {
  display: flex;
  align-items: center;
  font-size: 1.5rem;
  transform: translateY(500px);
  animation: SlideUp 0.8s ease forwards 0.9s;
}
.text-wrapper p {
  width: 370px;
  font-weight: 700;
  /* background: pink; */
}
.items {
  font-size: 3rem;
  opacity: 0;
  animation: fadeInOut 3s ease-in-out infinite;
}
.links {
  display: flex;
  justify-content: start;
  align-items: center;
  margin-top: 20px;
  font-size: 1.2rem;
  font-weight: 700;
  transform: translateY(500px);
  animation: SlideUp 0.8s ease forwards 1.1s;
}
.link {
  position: relative;
  padding-bottom: 2px;
}
.link a:hover {
  color: #000;
  transition: color 0.6s;
}

.link a::after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 100%;
  border: 1.8px solid #000;
  transform: scaleX(0);
  transition: transform 0.6s cubic-bezier(0.19, 1, 0.22, 1);
  transform-origin: right center;
}
.link a:hover::after {
  transform: scaleX(1);
  transform-origin: left center;
  transition-duration: 0.4s;
}

.divider {
  font-size: 1.5rem;
  padding: 0 15px;
}

.scroll {
  position: relative;
  transform: translateY(500px);
  animation: SlideUp 0.8s ease forwards 1.3s;
}

.scroll a span {
  position: absolute;
  bottom: -70px;
  left: 16px;
  width: 30px;
  height: 50px;
  margin-left: -15px;
  border: 2px solid #000;
  border-radius: 50px;
}
.scroll a span:hover {
  border: 2px solid #e6d6d6;
  transition: all 0.4s;
}
.scroll a span::before {
  content: "";
  position: absolute;
  top: 5px;
  left: 45%;
  width: 2px;
  height: 15px;
  background-color: black;
  border-radius: 30%;
  animation: Scroll ease 2s infinite;
}
.scroll a span:hover::before {
  background-color: #e6d6d6;
  transition: all 0.4s;
}
.circle {
  position: relative;
  height: 450px;
  width: 450px;
  background-color: #999;
  border-radius: 50%;
  overflow: hidden;
}
.circle::before {
  content: "";
  /* inset: 0px; */
  inset: -10px 100px;
  /* top: -10px;
  bottom: -10px;
  left: 100px;
  right: 100px; */
  position: absolute;
  background: linear-gradient(#000, #000);
  transition: 0.5s;
  animation: animatedCircle 4s linear infinite;
}
.circle::after {
  content: "";
  position: absolute;
  background-color: #fff;
  inset: 10px;
  border-radius: 50%;
}
.content {
  position: absolute;
  inset: 20px;
  border: 4px solid #000;
  border-radius: 50%;
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  flex-direction: column;
  line-height: 1.5;
  /* object-fit: cover; */
}
.content img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: 0.5s;
  text-align: center;
  pointer-events: none;
  z-index: 3;
}
.content h2 {
  position: relative;
  text-align: center;
  font-size: 15px;
  font-weight: 700;
  letter-spacing: 2px;
  text-transform: uppercase;
  padding: 10px 0;
}

/* .content h2 span {
  display: block;
  font-weight: 300;
  font-size: 30px;
  padding: 5px 0;
} */

.content a {
  position: relative;
  background-color: #222;
  color: #fff;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  border-radius: 20px;

  letter-spacing: 1px;
  transition: 0.5s;
}
.content a:hover {
  letter-spacing: 3px;
}

.circle:hover::before {
  inset: -20px 0px;
}
.circle:hover img {
  opacity: 0.1;
}

@keyframes SlideUp {
  0% {
    transform: translateY(500px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes fadeInOut {
  0% {
    transform: scale(0.8);
    opacity: 0;
  }
  30% {
    transform: scale(1.2);
    opacity: 1;
  }
  70% {
    transform: scale(1.2);
    opacity: 1;
  }
  100% {
    transform: scale(0.8);
    opacity: 0;
  }
}

@keyframes Scroll {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(10px);
  }
}

@keyframes animatedCircle {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
@media screen and (max-width: 890px) {
  .home-title h1 {
    font-size: 3rem;
  }
  .text-wrapper {
    font-size: 1.4rem;
  }
  .items {
    font-size: 2.2rem;
  }
  .links {
    font-size: 1rem;
    margin-top: 10px;
  }
  .divider {
    font-size: 1.2rem;
  }
  .circle {
    width: 340px;
    height: 340px;
  }
}

@media screen and (max-width: 768px) {
  .home {
    flex-direction: column-reverse;
    justify-content: space-around;
    line-height: 0.8;
  }
  .home-content {
    align-items: flex-start;
  }
  .text-wrapper p {
    width: 290px;
  }
  .circle {
    width: 300px;
    height: 300px;
  }
}
