.card {
  position: relative;
  display: block;
  height: 100%;
  border-radius: 40px;
  overflow: hidden;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.cards-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  font-family: "poppins";
  background-color: #fff;
  color: #555;
  z-index: 1;
  transform: translateY(100%);
  transition: 0.2s ease-in-out;
}

.card:hover .cards-overlay {
  transform: translateY(0);
}

.card-header {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 2em;
  border-top-right-radius: 40px;
  border-top-left-radius: 40px;
  background-color: white;
  transform: translateY(-100%);
  transition: 0.2s ease-in-out;
}

.card-icon {
  color: #222;
  margin-left: -20px;
}

.card-content a {
  background-color: #222;
  color: #fff;
  font-weight: 600;
  padding: 8px 12px;
  border-radius: 10px;
  letter-spacing: 1px;
  font-size: 0.9rem;
  transition: 0.3s;
}
.card-content a:hover {
  letter-spacing: 2px;
}

.card:hover .card-header {
  transform: translateY(0px);
}

.card-title {
  font-size: 1rem;
  font-family: sans-serif;
  margin-bottom: 12px;
}
.card-description {
  font-size: 0.9rem;
  padding: 0 2rem 2rem;
  margin: 0;
  overflow: hidden;
}
